.user-control-container {
    display:flex;
    justify-content: space-around;
    align-items: center;
    width:100%;
}

.submit-button {
    background-color: #52b345;
    border:none;
    color:white;
    font-size: 20px;
    font-weight: 400;
    font-family: Lexend, Rubik;
    padding: 10px 0px;
    border-radius: 5px;
    width:150px;
    cursor: pointer;
}

.show-solution-button {
    background-color: #bc872b;
    border:none;
    color:white;
    font-size: 20px;
    font-weight: 400;
    font-family: Lexend, Rubik;
    padding: 10px 0px;
    border-radius: 5px;
    width:150px;
    cursor: pointer;
}
.submit-button:hover {
    background-color: #ce9b42;
}
.submit-button:active {
    background-color: #e3a842;
}


.submit-button:hover {
    background-color: #5dc350;
}
.submit-button:active {
    background-color: #6dd35f;
}

.continue-button {
    background-color: #b650b3;
    border:none;
    color:white;
    font-size: 20px;
    font-weight: 400;
    font-family: Lexend, Rubik;
    padding: 10px 0px;
    width:150px;
    border-radius: 5px;
    cursor: pointer;
}
.continue-button:hover {
    background-color: #d774e2;
}
.continue-button:active {
    background-color: #df91f8;
}

.faded-user-control-button {
    background-color: #3a3b3e;
    border:none;
    height:50px; width:50px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s;
}
.faded-user-control-button:hover {
    background-color: #4f5157;
}
.transparent-user-control-button {
    background-color: transparent;
    border:none;
    height:50px; width:50px;
    border-radius: 5px;
    cursor: pointer;
}
.transparent-user-control-button:hover {
    background-color: #353839;
}

.disabled-button {
    background-color: transparent;
    border:none;
    filter: brightness(0.5);
    height:50px; width:50px;
    border-radius: 5px;
    cursor: not-allowed;
}

.user-ctrl-button-icon {
    height:24px; width:24px;
    filter:invert(70%);
}