.square-highlight-gre {
    background-color: #5fc252;
    border:none; outline: none;
    cursor: pointer;
}
.square-highlight-gre:hover {
    box-shadow: 0 0 15px rgba(11, 47, 3, 0.432) inset;
}
.square-highlight-yel {
    background-color: #e0d756;
    border:none; outline: none;
    cursor: pointer;
}
.square-highlight-yel:hover {
    box-shadow: 0 0 15px rgba(49, 49, 2, 0.432) inset;
}
.square-highlight-gra {
    background-color: #868686;
    border:none; outline: none;
    cursor: pointer;
}
.square-highlight-gra:hover {
    box-shadow: 0 0 15px rgba(35, 35, 35, 0.432) inset;
}
/* magenta for solution revealed squares */
.square-highlight-mag {
    background-color: #b650b3;
    border:none; outline: none;
    cursor: pointer;
}
.square-highlight-mag:hover {
    box-shadow: 0 0 15px rgba(45, 7, 56, 0.432) inset;
}
/* magenta for hinted squares */
.square-highlight-blu { 
    background-color: #5696c9;
    border:none; outline: none;
    cursor: pointer;
}
.square-highlight-blu:hover {
    box-shadow: 0 0 15px rgba(2, 35, 62, 0.432) inset;
}

.square-highlight-red {
    box-shadow: 0 0 25px rgba(250, 47, 1, 0.774) inset;
}

.square-obfuscated-white {
    background-color: #efefef;
    cursor: pointer;
}
.square-obfuscated-white:hover {
    box-shadow: 0 0 15px rgba(22, 22, 22, 0.432) inset;
}
.square-obfuscated-black {
    background-color: #333333;
    cursor: pointer;
}
.square-obfuscated-black:hover {
    box-shadow: 0 0 15px rgba(233, 233, 233, 0.432) inset;
}
.occupied-square:hover {
    cursor: pointer;
    box-shadow: 0 0 15px rgba(19, 19, 19, 0.653) inset;
}


.square {
    display: flex;
    justify-content: center;
    align-items: center;
    position:relative;
    border: none; outline: none;
    height: 80px; width: 80px;
}
.board-piece-image {
    height:100%; width:100%;
    border: none;
}
.square-secondary {
    display: flex;
    justify-content: center;
    align-items: center;
    position:relative;
    border: none; outline: none;
    height:40px; width:40px;
}
.board-piece-image-secondary {
    height:40px; width:40px;
}
.square-preview {
    display: flex;
    justify-content: center;
    align-items: center;
    position:relative;
    border: none; outline: none;
    height:16px; width:16px;
}
.board-piece-image-preview {
    height:16px; width:16px;
}